@import "../../../style/theme/default/var.css";

@component-namespace member{
  @b division-block{
    margin-bottom:var(--theme-margin-base);
  > .el-button {
    width:100%;
    text-align: center;
  }
}
}

@component-namespace storecheck{
      @b wrap{
      /*  padding: var(--theme-padding-middle);*/
        background-color: var(--theme-background-color-primary);
        & .el-collapse-item > .el-collapse-item__wrap > .el-collapse-item__content {
          /*padding-top:0;*/
         }
      & > .el-collapse-item > .el-collapse-item__header > .el-collapse-item__header__arrow {
        display: none;
          }
      & > .el-collapse-item > .el-collapse-item__header > .el-collapse-item__header__caret {
        float: right;
            margin-top: 13px;
        &:before{
           content: "\E600";
         }
          /*padding-top:0;*/
        }
      }
      @b cate{
      &.el-collapse {
        border:none;
      & .el-collapse-item__header{
          display: inline-block;
          min-width:200px;
          border-bottom:1px solid var(--theme-color-primary);
          height: auto;
          line-height: normal;
          padding:0;
          background-color: transparent;
        }
        & .el-collapse-item__content{
        padding-left:0;
        padding-right:0;
          }
        & .el-collapse-item__header__arrow {
           display: none;
        }
        .el-collapse-item.is-active>.el-collapse-item__header .storecheck-cate__arrow {
          -ms-transform: rotate(-180deg);
          transform: rotate(-180deg);
        }
      & .el-collapse-item__wrap{
         background: transparent;
         border-bottom:none;
        }
        & .el-multi-node__content{
        /*height: 26px;*/
        /*line-height:26px;*/
          }
        .el-multi-node>.el-multi-node__children{
          background-color: var(--theme-background-color-primary);
          padding-bottom: 10px;
        }
        .el-multi-node-1>.el-multi-node__children{
          border-color:var(--theme-border-color-primary);
          padding-top: 10px;
          & > .el-multi-node__lastleaf{
            margin-top: -10px;
          }
        }
        .el-multi-node-1.is-expanded>.el-multi-node__content:before{
          border-bottom-color: var(--theme-border-color-primary);
        }
        .el-multi-node-1.is-expanded>.el-multi-node__content:after{
          border-bottom-color: var(--theme-background-color-primary);
        }
        .el-multi-node__lastleaf{
          padding-bottom:10px;
        }
        .el-multi-node__lastleaf-item{
          display: inline-block;
          height: 26px;
          line-height:26px;
          /*font-size: 14px;*/
          position: relative;
          margin-bottom:0;
        }
        .el-multi-node .el-multi-node__label,
        .el-multi-node.el-multi-node-1>.el-multi-node__content>.el-multi-node__label {
          max-width:inherit;
        }
      }
      @e title{
        display:inline-block;
        padding:var(--theme-padding-base) 0;
      }
      @e number{
       margin: 0 var(--theme-margin-small);
       font-size:var(--theme-font-size-small);
        color: var(--theme-font-color-primary);
         > b {
          font-weight: normal;
          color: var(--theme-font-color-secondary);
        }
      }
      @e arrow{
        transition: transform .3s;
        font-size:var(--theme-font-size-small);
       color:var(--theme-background-color-secondary);
        padding:0 5px;
      }
      @e icon-menu{
        font-size: 12px;
        padding:0 5px;
      }

      @e leaflist{
        background-color: var(--theme-background-color-primary);
        padding: var(--theme-padding-base) 0;
        border:1px solid var(--theme-border-color-primary);
       /* & > close {

            }*/
      }


      @e local {
        margin-bottom:var(--theme-margin-base);
        color:var(--theme-color-primary);
      }
      @e item{
        height: 26px;
        line-height: 26px;
        /*margin-bottom:var(--theme-margin-base);*/
        position:relative;
        .tmp-mark{
          display:none;
          position: absolute;
          left:0;
          top:50%;
          -webkit-transform: translate(0,-50%);
          -ms-transform: translate(0,-50%);
          transform: translate(0,-50%);
        }
        @e value{
          /*margin-left:28px;*/
          margin-right: var(--theme-margin-base);
          font-size: var(--theme-font-size-small);
        }
      }
    }
}




